@import "color-picker";
@import "media";

.redux-warning-primary{
    color: #fff !important;
    border-color: #dc3545 !important;
    background: #dc3545 !important;

    &:hover,
    &:focus{
        border-color: #bd2130 !important;
        background: #bd2130 !important;
    }

    &:focus{
        box-shadow: 0 0 0 1px #fff, 0 0 0 3px #bd2130 !important;
    }

    &:active{
        border-color: #bd2130 !important;
        background: #bd2130 !important;
    }
}

.spinner {
    visibility: hidden;
    display: block;
}

.spinner.is-active {
    visibility: visible;
}

.redux-main .description {
    margin-top: 7px;
}

.redux-container {
    #redux-intro-text {
        padding-left:20px;
    }

    .redux-container-button_set,
    .redux-container-palette {
        .ui-icon {
            height: auto;
        }
    }

    .form-table > tbody > tr > th {
        width: 30%;
    }

    .form-table th,
    .form-table td {
        margin: 0;
        padding: 0;
        width: auto;
    }

    .redux_field_th {
        font-weight: 600;
        padding: 20px 10px 20px 0;
        display: block;
        span {
            &:first-child {
                font-weight: normal;
                display: block;
                color: #666;
            }
        }
    }

    #ui-datepicker-div {
        display: none;
    }

    background-color: #fff; /* Old browsers */
    background-repeat: repeat-x; /* Repeat the gradient */
    background-image: -moz-linear-gradient(top, #fff 0%, #f2f2f2 100%); /* FF3.6+ */
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #fff), color-stop(100%, #f2f2f2)); /* Chrome,Safari4+ */
    background-image: -webkit-linear-gradient(top, #fff 0%, #f2f2f2 100%); /* Chrome 10+,Safari 5.1+ */
    background-image: -ms-linear-gradient(top, #fff 0%, #f2f2f2 100%); /* IE10+ */
    background-image: -o-linear-gradient(top, #fff 0%, #f2f2f2 100%); /* Opera 11.10+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#fff", endColorstr="#f2f2f2", GradientType=0); /* IE6-9 */
    background-image: linear-gradient(to bottom, #fff 0%, #f2f2f2 100%); /* W3C */
    border: 1px solid #dedede;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
    -moz-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.4);
    margin-top: 5px;
    overflow: hidden;

    a {
        &:focus {
            box-shadow: none;
        }
    }

    #redux-header,
    #redux-footer {
        text-align: right;
        padding: 6px 10px;
    }

    #redux-header {
        border-bottom-width: 3px;
        border-bottom-style: solid;

        .display_header {
            float: left;
            margin: 20px 10px;

            img {
                transition: all 0.1s ease-in-out;
            }

            h2 {
                display: inline-block;
                font-style: normal;
                padding-right: 5px;
            }

            .redux-dev-mode-notice-container {
                position: absolute;
                top: 67px;
                left: 20px;
                bottom: auto;
                width: auto;
            }

            span {
                color: #888;

                &.redux-dev-mode-notice {
                    background-color: #f0ad4e;
                    display: inline;
                    padding: 0.2em 0.5em 0.2em;
                    font-weight: 700;
                    line-height: 1;
                    color: #fff !important;
                    text-align: center;
                    white-space: nowrap;
                    vertical-align: baseline;
                    border-radius: 0.25em;
                }
            }
        }

        .icon32 {
            float: right;
            margin: 16px 16px 0;
        }
    }

    #redux-footer {
        border-top: 1px solid #e7e7e7;
        z-index: 999;

        #redux-share {
            float: left;
            line-height: 28px;
            font-size: 15px;

            a {
                text-decoration: none;
                margin-right: 10px;

                img {
                    margin-bottom: -3px;
                }
            }
        }
    }

    .notice-green {
        margin: 0;
        border-bottom: 1px solid #e7e7e7;
        background-color: #dff0d8;
        color: #468847;
        padding: 8px 35px 8px 14px;
        text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    }

    .notice-blue {
        margin: 0;
        border-bottom: 1px solid #bce8f1;
        background-color: #d9edf7;
        color: #3a87ad;
        padding: 8px 35px 8px 14px;
        text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    }

    .notice-yellow {
        margin: 0;
        border-bottom: 1px solid #e7e7e7;
        background-color: #fcf8e3;
        color: #c09853;
        padding: 8px 35px 8px 14px;
        text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    }

    .notice-red,
    .redux-field-errors {
        margin: 0;
        border-bottom: 1px solid #e7e7e7;
        background-color: #f2dede;
        color: #b94a48;
        padding: 8px 35px 8px 14px;
        text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    }

    .redux-field-error {
        input,
        textarea,
        input[type="checkbox"] {
            background-color: #fff6f6;
            color: #a00;
        }
    }

    .redux-field-warning {
        input,
        textarea,
        input[type="checkbox"] {
            background-color: #fcf8e3;
            color: #444;
        }
    }

    .redux-field-errors,
    .redux-field-warnings,
    .redux-save-warn {
        display: none;
    }

    .sticky-save-warn {
        min-height: 76px;

        .redux-save-warn {
            position: fixed;
            top: 32px;
            right: 21px;
            left: 183px;
            opacity: 1;
            z-index: 9999;
        }
    }

    #info_bar {
        background: #f3f3f3;
        border-bottom: 1px solid #dedede;
        padding: 6px 10px 6px 6px;
        text-align: right;
        -moz-box-shadow: inset 0 1px 0 #fcfcfc;
        -webkit-box-shadow: inset 0 1px 0 #fcfcfc;
        box-shadow: inset 0 1px 0 #fcfcfc;
    }

    .redux-group-tab {
        display: none;
        margin-bottom: 15px;

        .redux-theme-data {
            padding: 20px 0;
            border-top: 1px solid #e7e7e7;

            &.theme-description {
                padding: 10px 0;
                border-width: 0;
            }

            &.theme-uri,
            &.theme-author,
            &.theme-version {
                padding: 0;
                border-width: 0;
            }
        }

        h3 {
            margin-top: 0;
            line-height: 2em;
            border-bottom: 1px solid #e7e7e7;
        }

        .redux-section-desc {
            margin-bottom: 15px;
            color: #666;
        }
    }

    .redux-action_bar {
        float: right;
        .spinner {
            float: left;
            margin-top: 7px;
            margin-left: 10px;
            right: 5px;
            position: relative;
        }

        #redux-defaults-top,
        #redux-defaults-bottom {
            margin-left:2px;
        }

        #redux-defaults-section-top,
        #redux-defaults-section-bottom {
            margin-left:2px;
        }
    }

    .expand_options {
        cursor: pointer;
        display: block;
        height: 22px;
        width: 21px;
        float: left;
        font-size: 0;
        text-indent: -9999px;
        margin: 1px 0 0 5px;
        border: 1px solid #bbb;
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
        border-radius: 2px;
        background: url(data:image/png;base64,) no-repeat -2px -26px;

        &.expanded {
            background: url(data:image/png;base64,) no-repeat -2px -1px;
        }

        &:hover {
            border-color: #888;
        }
    }

    .sticky-footer-fixed {
        background: #f3f3f3;
        border-top: 1px solid #dedede !important;
        -moz-box-shadow: inset 0 1px 0 #fcfcfc;
        -webkit-box-shadow: inset 0 1px 0 #fcfcfc;
        box-shadow: inset 0 1px 0 #fcfcfc;
    }

    .redux-sidebar,
    .redux-main {
        min-height: 300px;

    }
    .redux-main {
        background: #fff;
        margin-left: 201px;
        border-left: 1px solid #d8d8d8;
        padding: 10px 20px;
        -moz-box-shadow: inset 0 1px 0 #fff;
        -webkit-box-shadow: inset 0 1px 0 #fff;
        box-shadow: inset 0 1px 0 #fff;
        position: relative;

        fieldset.disabled {
            padding-top: 10px;
        }
        .redux-group-tab.disabled {
            padding-top: 1px;
        }

        fieldset.disabled,
        .redux-group-tab.disabled {
            position: relative;

            &::before {
                color: #fff;
                font-family: Elusive-Icons, serif;
                content: "ï†¤";
                font-size: 38px;
                position: absolute;
                z-index: 1000;
                text-shadow: 2px 2px #0e0e0e;
                left: 50%;
                top: 50%;
                margin-top: -25px;
                margin-left: -25px;
            }
        }

        .disabled {
            pointer-events: none;
            background: rgba(0, 0, 0, 0.25);
            opacity: 0.45;
            padding-left: 10px !important;
            padding-right: 10px !important;
            -webkit-touch-callout: none; /* iOS Safari */
            -webkit-user-select: none; /* Safari */
            -moz-user-select: none; /* Old versions of Firefox */
            -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
        }

        #redux_ajax_overlay {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            -moz-opacity: 0.1;
            opacity: 0.1;
            filter: progid:DXImageTransform.Microsoft.Alpha(opacity=10);
            filter: alpha(opacity=10);
            background: #000;
            z-index: 200;
            display: none;
        }

        .form-table.no-border {
            border-top: none;
        }

        .form-table tr {
            border-bottom: 1px solid #e7e7e7;

            &:last-child {
                border-bottom: none !important;
            }

            th,
            td {
                color: #333;
            }
        }

        .form-table tr td {
            table.mceLayout,
            table.mceLayout tr,
            table.mceLayout tr td {
                padding: 0;
                border-width: 0;
            }

            .redux-th-warning {
                font-size: 1em;
                color: #c09853;
                font-weight: normal;
                display: block;
                margin-top: 10px;
            }

            .redux-field-warning {
                border-color: #c09853;
                margin-top: 10px;
            }

            .redux-th-error {
                font-size: 1em;
                color: #b94a48;
                font-weight: normal;
                display: block;
                margin-top: 10px;
            }
        }

        input {
            line-height: 19px;

            &.large-text {
                width: 95%;
            }
        }

        .hide {
            display: none;
        }

        .redux-field-container {
            padding: 20px 0;
        }

        .mini,
        input[type="text"].mini {
            width: 60px;
            text-align: center;
        }

        img {
            max-width: 100%;
            height: auto;
            width: auto !important;
        }

        .select2-default {
            width: auto !important;
        }

        .showDefaults {
            display: block;
            font-weight: normal;
            font-size: 0.8em;
            color: #888;
        }

        span.description {
            display: block;
            font-style: normal;
            font-weight: 400;
        }

        #redux-system-info textarea {
            min-height: 730px;
            width: 100%;
        }

        .field-desc {
            clear: both;
            font-size: 13px;
        }

        .data-full li {
            width: 100%;
        }

        .data-half li {
            width: 50%;
            float: left;
        }

        .data-third li {
            width: 33.3%;
            float: left;
        }

        .data-quarter li {
            width: 25%;
            float: left;
        }

        .ui-helper-hidden-accessible {
            top: inherit;
        }

        .form-table {
            clear: none !important;
            margin-top: 0 !important;

            tr:first-child th,
            tr:first-child td {
                padding-top: 0;
            }
        }

        .input-append input {
            border-right: 0;
            margin-bottom: 0;
            border-top-right-radius: 0 !important;
            border-bottom-right-radius: 0 !important;
            margin-right: 0;
            float: left;
            margin-top: 0;
            display: block;
        }

        .input-append .add-on {
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
            margin-left: -2px;
            padding-top: 4px !important;
            padding-bottom: 2px !important;
        }

        .input-prepend input {
            border-left: 0;
            margin-bottom: 0;
            border-top-left-radius: 0 !important;
            border-bottom-left-radius: 0 !important;
            margin-left: 0;
            padding-top: 2px;
            padding-bottom: 5px;
            float: left;
            margin-top: 0;
            display: block;
        }

        .input-prepend .add-on {
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
            float: left;
        }

        .input-append {
            margin-right: 10px;
            font-size: 0;
            white-space: nowrap;
            float: left;
            display: inline-block;
            margin-bottom: 6px;

        }

        .input-append .add-on,
        .input-prepend .add-on {
            width: auto;
            display: inline-block;
            min-width: 16px;
            padding: 3px 4px;
            font-size: 12px;
            font-weight: 400;
            line-height: 22px;
            text-align: center;
            text-shadow: 0 1px 0 #fff;
            background-color: #eee;
            border: 1px solid #7e8993;
        }
        .input-append .add-on {
            border-left-color: #ccc;
        }
        .input-prepend .add-on {
            border-right-color: #ccc;
        }

        .input-prepend {
            font-size: 0;
            white-space: nowrap;
            float: left;
            display: inline-block;
            margin-bottom: 6px;
        }

        pre {
            white-space: pre-wrap; /* css-3 */
            white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
            white-space: pre-wrap; /* Opera 4-6 */
            white-space: -o-pre-wrap; /* Opera 7 */
            word-wrap: break-word; /* Internet Explorer 5.5+ */
        }
    }
}

/* redux-container */
.no-js {
    border: 1px solid #ffbaba;
    margin: 0;
    border-bottom: 1px solid #e7e7e7;
    background-color: #f2dede;
    color: #b94a48;
    padding: 8px 35px 8px 14px;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
}

/* main */
.redux-sidebar {
    width: 202px;
    float: left;

    // Flyout menu
    .redux-group-tab-link-li {
        &.hasSubSections {
            &:hover {
                .el-rotate {
                    -webkit-transition: 0.1s ease-in-out;
                    -moz-transition: 0.1s ease-in-out;
                    -o-transition: 0.1s ease-in-out;
                    transition: 0.1s ease-in-out;

                    -ms-transform: rotate(270deg);
                    transform: rotate(270deg);
                }
            }
        }
    }

    .redux-group-menu {
        margin-top: 0 !important;
        background: #f2f2f2;

        li {
            margin-top: 0;

            &.active a,
            &.active a:hover,
            &.activeChild a,
            &.activeChild a:hover {
                background: #fcfcfc;
                color: #269ad6;
                width: 184px;
                opacity: 1;
            }

            &.active a li a {
                background: #333;
                padding-left: 5px;
            }

            &.divide {
                padding: 0;
                border-width: 1px 0;
                border-style: solid;
                border-bottom-color: #e7e7e7;
                border-top-color: #f9f9f9;
            }

            a:first-child {
                border-top: none;
            }

            a {
                display: block;
                padding: 10px 4px 10px 14px;
                background: transparent;
                border-width: 1px 0;
                border-style: solid;
                border-bottom-color: #e7e7e7;
                border-top-color: #f9f9f9;
                opacity: 0.7;
                color: #555;
                font-weight: 600;
                text-decoration: none;
                -webkit-transition: none;
                transition: none;

                &.custom-tab {
                    background: #f6f6f6;
                }

                img {
                    width: 16px;
                    height: 16px;
                    position: absolute;
                    left: 15px;
                }

                &:hover {
                    background: #e5e5e5;
                    color: #777;
                    opacity: 1;
                    box-shadow: inset 4px 0 0 0 currentColor;
                    transition: box-shadow 0.1s linear;
                }
            }
        }
    }

    .redux-menu-warning,
    .redux-menu-error,
    .hasSubSections .extraIconSubsections {
        display: inline-block;
        float: right;
        padding: 6px 7px 4px 7px;
        margin-left: 4px;
        font-family: sans-serif;
        font-size: 9px;
        font-weight: 600;
        line-height: 9px;
        border-radius: 10px;
        -moz-border-radius: 10px;
        -webkit-border-radius: 10px;
        border: 0 solid transparent;

        i {
            margin-left: -3px;
            margin-top: -3px;
        }
    }

    .redux-menu-error {
        background-color: rgb(185, 74, 72);
        color: rgb(242, 222, 222);
    }

    .redux-menu-warning {
        background-color: #c09853;
        color: #fcf8e3;
    }

    ul {
        .subsection {
            display: none;
        }
    }

    .redux-group-tab-link-a {
        position: relative;
        outline: 0;

        i {
            vertical-align: middle;
            font-size: 1.35em;
            position: absolute;
        }

        span {
            display: block;
            &.group_title {
                padding-left: 30px;
            }
        }
    }

    .redux-group-menu > li.redux-section-hover > a::after {
        right: 0;
        border: 9px solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-right-color: #fff;
        top: 50%;
        margin-top: -8px;
    }

    .redux-group-tab-link-li {
        a.hasError span.group_title {
            padding-right: 25px;
        }

        &.hasSubSections {
            &.redux-section-hover {
                position: relative;

                > a {
                    background-color: #fff;

                    &::after {
                        border-right-color: #46403c;
                    }
                }

                .subsection {
                    background-color: #46403c;
                    display: block !important;
                    z-index: 10000;
                    position: absolute;
                    top: 0;
                    left: 201px;
                    width: 200px !important;
                    -webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
                    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);

                    a {
                        padding: 8px 15px;
                        width: 170px;
                    }

                    .redux-group-tab-link-li {
                        &.hasIcon {
                            .group_title {
                                padding-left: 30px !important;
                            }
                        }

                        .redux-group-tab-link-a {
                            color: #fff;

                            .group_title {
                                padding-left: 0;
                            }
                        }
                    }
                }
            }

            .el-rotate {
                -webkit-transition: 0.1s ease-in-out;
                -moz-transition: 0.1s ease-in-out;
                -o-transition: 0.1s ease-in-out;
                transition: 0.1s ease-in-out;
            }
        }
    }

    #redux-header {
        text-align: center;

        .display_header {
            float: none;
        }
    }
}

/* sidebar */
@media only screen and (max-width: 600px) {
    .redux-sidebar {
        .redux-group-menu li {
            &.hasSubSections.redux-section-hover .subsection {
                display: none !important;
            }
        }
    }
}

.mp6 {
    .icon-themes {
        display: none;
    }

    .redux-container {
        #info_bar {
            padding: 6px 10px 6px 6px;

            a {
                margin-top: 2px;
            }
        }
    }
}

.redux-timer {
    text-align: center;
    font-size: 10px;
    color: #888;
}

.wrap {
    margin-top: 0;
}

@media screen and (max-width: 600px) {
    .redux-sidebar {
        width: 44px;

        .extraIconSubsections {
            display: none !important;
        }

        .redux-group-menu li a,
        .redux-group-menu li a:hover,
        .redux-group-menu li.active a,
        .redux-group-menu li.active a:hover,
        .redux-group-menu li.activeChild a,
        .redux-group-menu li.activeChild a:hover {
            width: auto;
        }

        .redux-group-tab-link-a {
            position: relative;

            i {
                position: inherit;
            }

            span {
                display: none;
                position: absolute;
                top: 0;
                left: 44px;
                padding: 12px;
                width: 200px;
                background: #eee;
                border: 1px solid #ccc;
                -webkit-box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
                -moz-box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
                box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
                border-width: 1px 1px 1px 0px;
                z-index: 3;
            }

            &:hover > span {
                display: block;
            }
        }
    }

    .redux-main {
        margin-left: 43px;
        width: auto;
        max-width: 100%;
    }

    table.form-table,
    .form-table > thead,
    .form-table > tbody,
    .form-table > tbody > tr > th,
    .form-table > tbody > tr > td,
    .form-table > tbody > tr {
        display: block;
        width: 100% !important;
        padding: 0 !important;
    }

    .form-table > tbody > tr > th,
    .form-table > tbody > tr > td {
        padding: 10px !important;
    }

    .form-table > tbody > tr > th,
    .form-table > tbody > tr > td {
        padding: 10px !important;
    }
}

//mp6 fixes
@media screen and (max-width: 782px) {
    .form-table > tbody > tr > th {
        width: 100%;
    }
    .redux_field_th {
        padding-bottom: 0;
    }
    .mp6 {
        .redux-container {
            #info_bar {
                height: auto;
                padding-bottom: 1px;

                a {
                    margin-top: 5px;
                }
            }
        }
    }
    .redux-container-switch label {
        padding: 5px 10px !important;
    }

    .redux-container-button_set label {
        padding: 12px 10px;
    }

    .redux-container #redux-footer #redux-share {
        line-height: 34px;
    }
}

pre {
    overflow: hidden;
