/* Default admin theme */
#redux-header h2 {
    color: #fff;
}

.ui-datepicker-header .ui-icon {
    background-image: url(data:image/png;base64,) !important;
}

// WP Engine CSS fix
.redux-sidebar .icon-large,
.redux-main .icon-large {
    background-image: inherit !important;
    width: inherit;
    height: inherit;
}

.redux-main dd,
.redux-sidebar li {
    margin-bottom: 0 !important;
}

.fully-expanded {
    .redux-sidebar {
        margin-left: -500px;
    }
    .redux-main {
        margin-left: 0;
    }
    .redux-group-tab {
        display: block;
    }
}

#redux-header {
    position: relative;
}

// Modern Theme
.redux-main {
    position: relative;

    #redux-sticky {
        min-height: 32px;
        margin: -10px -20px 8px;

        #info_bar {
            height: 32px;
            .expand_options {
                margin-top: 4px;
            }
        }
    }

    .redux_field_search {
        top: 8px;
        right: 290px;
    }

    #redux-footer-sticky {
        margin-left: -20px;
        margin-right: -20px;
        margin-bottom: -10px;
    }
}

.redux-qtip {
    z-index: 999999 !important;
}

.select2-container--default .select2-selection--single .select2-selection__clear {
    font-size: 1.2em;
    top: -1px;
}

.redux-sidebar .redux-group-menu {
    li {
        &.active,
        &.activeChild {
            border-left: 0 none;

            a {
                .extraIconSubsections {
                    display: none;
                }
            }

            &.hasSubSections {
                .redux-menu-error {
                    display: block;
                }

                .subsection {
                    .redux-menu-error {
                        margin-right: 2px;
                    }
                }

                .active {
                    a {
                        &::after {
                            right: 0;
                            border: solid 8px transparent;
                            content: "\0020";
                            height: 0;
                            width: 0;
                            position: absolute;
                            pointer-events: none;
                            border-right-color: #fff;
                            top: 50%;
                            margin-top: -8px;
                        }
                    }
                }

                a {
                    -webkit-transition: all 0.2s;
                    -moz-transition: all 0.2s;
                    transition: all 0.2s;
                    color: #fff;
                    width: auto;
                    border-bottom: 0;
                }

                ul.subsection li {
                    border-top: 0 none !important;

                    &.active a:hover {
                        color: #fff;
                    }

                    a {
                        width: auto;
                        border-top: 0 !important;
                        padding: 7px;
                        color: #fff;
                        padding-left: 15px;
                        -webkit-transition: all 0.2s;
                        -moz-transition: all 0.2s;
                        -ms-transition: all 0.2s;
                        -o-transition: all 0.2s;
                        transition: all 0.2s;

                        span.group_title {
                            padding-left: 5px !important;
                        }
                    }

                    &.hasIcon {
                        a {
                            padding-left: 14px;

                            span.group_title {
                                padding-left: 30px !important;
                            }

                        }
                    }
                }
            }
        }

        &.hasSubSections {
            .redux-menu-error {
                display: none;
                margin-right: 5px;
            }

            a {
                &.hasError {
                    .extraIconSubsections {
                        background-color: rgb(185, 74, 72);
                        color: rgb(242, 222, 222);
                    }
                }

                .extraIconSubsections {
                    border-radius: 10px;
                    -moz-border-radius: 10px;
                    -webkit-border-radius: 10px;
                    border: 0 solid transparent;
                    font-size: 9px;
                    height: 9px;
                    line-height: 9px;
                    margin-right: 5px;
                    padding: 6px 7px 4px 7px;
                    width: 5px;
                }
            }
        }

        &.active {
            &.hasSubSections {
                a {
                    position: relative;

                    &::after {
                        right: 0;
                        border: solid 8px transparent;
                        content: "\0020";
                        height: 0;
                        width: 0;
                        position: absolute;
                        pointer-events: none;
                        border-right-color: #fff;
                        top: 50%;
                        margin-top: -8px;
                    }

                }

                ul.subsection li a {
                    &::after {
                        border: 0 none !important;
                        content: "\0020" !important;

                    }
                }
            }
        }
    }
}

div.redux-star-holder {
    position: relative;
    height: 19px;
    width: 100px;
    font-size: 19px;
}

div.redux-star {
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: transparent;
    letter-spacing: 1ex;
    border: none;

    img {
        width: 19px;
        height: 19px;
        border-left: 1px solid #f1f1f1;
        border-right: 1px solid #f1f1f1;
        display: block;
        position: absolute;
        right: 0px;
        border: none;
        text-decoration: none;
    }

    &.redux-star-rating {
        background-color: #fc0;
    }

    &.redux-star1 {
        width: 20%;
    }

    &.redux-star2 {
        width: 40%;
    }

    &.redux-star3 {
        width: 60%;
    }

    &.redux-star4 {
        width: 80%;
    }

    &.redux-star5 {
        width: 100%;
    }
}

